/* Updated Login.css */

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(
    to right,
    #84ffc9,
    #aab2ff,
    #eca0ff
  ); /* Gradient background */
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh; /* Full height of the viewport */
}

.text-center {
  padding: 2%;
  color: #374955; /* White text color */
}

.check-in {
  width: 500px;
  border-radius: 10%;
  padding: 1%;
}

.form-group {
  margin-bottom: 15px;
}

.center-button {
  margin-bottom: 15px;
  display: flex;
  justify-content: center; /* Center content horizontally */
}

label {
  display: block;
  margin-bottom: 5px;
  padding: auto;
  color: #023047; /* White text color for labels */
}

input {
  width: 50%;
  padding: 10px;
  border: none;
  border-bottom: 2px solid white; /* Only bottom border visible */
  background: transparent; /* Transparent background */
  color: #023047; /* White text color */
  transition: border-bottom-color 0.3s ease;
}

input:focus {
  outline: none;
  border-bottom-color: #ecf0f1; /* Change border color on focus */
}

#login-button {
  background: #27ae60; /* Pleasant green color */
  color: white;
  padding: 10px;
  width: 30%;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

#login-button:hover {
  background: #219653; /* Darker green color on hover */
}
