/* Menu.css */

/* Container Style */
.border-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #3b5998; /* Updated background color to a more aesthetic blue */
  padding: 10px 20px;
}

/* Menu Style */
.menu {
  list-style-type: none;
  display: flex;
  flex: 1;
  margin: 0;
  padding: 0;
  justify-content: space-evenly;
}

.menu li {
  padding: 8px 12px;
}

.menu li a {
  color: white;
  text-decoration: none;
  font-size: 16px; /* Increase font size for better readability */
}

.menu li a:hover {
  color: #ddd;
  text-decoration: none; /* Remove underline on hover */
}

/* Logo Style */
.navbar-logo {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.navbar-logo img {
  height: 60px; /* Increase logo size */
  margin-right: 20px;
}

/* Search Style */
.search {
  display: flex;
  align-items: center;
  width: 20%;
}

.search input[type="search"] {
  padding: 8px;
  margin-right: 8px;
  border: 1px solid #ddd; /* Add a subtle border for better visibility */
  border-radius: 4px;
}

.search input[type="submit"] {
  padding: 8px 15px;
  background-color: #4caf50; /* Green color for the submit button */
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.search input[type="submit"]:hover {
  background-color: #45a049; /* Darker green color on hover */
}
