/* Bar.css */

/* Container Style */
.main-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #8b9dc3; /* Updated background color to a green shade */
  padding: 15px 20px; /* Increased padding for better spacing */
}

/* Left and Right Sections */
.float-left,
.float-right {
  display: flex;
  align-items: center;
}

.float-left div,
.float-right div {
  margin: 0 15px; /* Added more spacing around the divs */
}

/* Links/Buttons Style */
.float-left a,
.float-right a {
  display: inline-block;
  padding: 12px 20px; /* Increased padding for larger buttons */
  margin: 0 8px;
  text-decoration: none;
  color: white;
  background-color: #333; /* Dark background color for buttons */
  border: 2px solid #333;
  border-radius: 20px; /* More rounded corners */
  transition: all 0.3s ease;
}

.float-left a:hover,
.float-right a:hover {
  background-color: #555; /* Darker background color on hover */
}

/* Cart Section Style */
.float-right b {
  font-weight: bold;
  color: #fff; /* White color for the cart item count */
}

/* Clear Floats */
.float-clear {
  clear: both;
}
